@charset "UTF-8";
@font-face {
  font-family: 'Source Sans Pro';
  src: url("../fonts/SourceSansPro-Regular.woff2") format("woff2"), url("../fonts/SourceSansPro-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Source Sans Pro';
  src: url("../fonts/SourceSansPro-Bold.woff2") format("woff2"), url("../fonts/SourceSansPro-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: 'Source Sans Pro';
  src: url("../fonts/SourceSansPro-Italic.woff2") format("woff2"), url("../fonts/SourceSansPro-Italic.woff") format("woff");
  font-weight: normal;
  font-style: italic; }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  font-family: inherit; }

html,
body {
  min-height: 100vh;
  height: auto; }

body {
  position: relative;
  box-sizing: border-box;
  font-family: 'Source Sans Pro', sans-serif;
  background: #f6f3fc; }
  body::before {
    content: "";
    position: absolute;
    top: 0;
    z-index: -1;
    clip-path: polygon(100% 0, 100% 60%, 78% 79%, 41% 60%, 0 82%, 0 0);
    width: 100%;
    height: 60vh;
    background: #7538f8; }

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

main {
  flex-direction: column; }

form {
  width: 95vw; }
  @media only screen and (min-width: 580px) {
    form {
      width: 420px; } }

.field {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center; }

.tip-amount,
.total-amount {
  display: flex;
  align-items: center;
  justify-content: flex-end; }

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center; }

.padding-y {
  padding: 20px 0; }

.padding-xy {
  padding: 20px; }

.margin-b {
  margin-bottom: 20px; }

.text-md {
  font-size: 16px; }
  @media only screen and (min-width: 580px) {
    .text-md {
      font-size: 18px; } }

.text-lg {
  font-size: 18px; }
  @media only screen and (min-width: 580px) {
    .text-lg {
      font-size: 24px; } }

.text-light {
  color: #fff; }

.text-dark {
  color: #313131; }

h1 {
  font-weight: 400;
  text-align: center; }

h3 {
  font-weight: 700; }

input[type="text"]::placeholder {
  font-size: 16px;
  font-style: italic;
  color: #313131; }

button {
  font-weight: 400; }

.tip-amount,
.total-amount {
  font-weight: 400; }

form {
  background: #fff;
  border-radius: 5px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }
  form input {
    width: 100%;
    height: 40px;
    padding: 6px 25px 6px 10px;
    background-color: #e6dbff;
    border: none; }
    form input:focus {
      background: #fff; }
  form select {
    width: 100%;
    height: 40px;
    padding: 6px;
    background-color: #e6dbff;
    border: none;
    cursor: pointer; }
    form select:focus {
      background: #fff; }
  form button {
    display: block;
    width: 100%;
    padding: 10px 0;
    border: none;
    background: #7538f8;
    border-radius: 3px;
    box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.3);
    cursor: pointer; }

.divider {
  height: 1px;
  border: none;
  border-top: 1px dashed #7538f8; }

.formatted {
  position: relative; }
  .formatted::after {
    content: '€';
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    color: #7538f8;
    font-weight: 400;
    font-size: 22px; }

.btn-wrap {
  padding: 0 20px 20px 20px; }

.tip-amount,
.total-amount {
  width: 100%;
  height: 40px;
  background-color: #e6dbff;
  padding-right: 25px; }
